import { html } from 'lit-html';
import { state } from '../state/state.js';
import { addToCart } from '../state/state.js';

export const Menu = () => {
  if (!state.pizzas.length) {
    return html`<p>Loading pizzas...</p>`;
  }

  return html`
    <section id="menu">
      <h2>Menu</h2>
      <div id="pizza-list">
        ${state.pizzas.map(
          (pizza) => html`
            <div class="pizza-item">
              <img src="${pizza.image}" alt="${pizza.name}" width="100">
              <h3>${pizza.name}</h3>
              <p>$${pizza.price}</p>
              <div>
                <label>Toppings:</label>
                ${pizza.toppings.map(
                  (topping) => html`
                    <label>
                      <input
                        type="checkbox"
                        value="${topping}"
                        checked
                        @change=${(e) => {
                          if (e.target.checked) {
                            pizza.selectedToppings.push(topping);
                          } else {
                            pizza.selectedToppings = pizza.selectedToppings.filter(t => t !== topping);
                          }
                        }}
                      />
                      ${topping}
                    </label>
                  `
                )}
              </div>
              <button @click=${() => addToCart(pizza)}>Add to Cart</button>
            </div>
          `
        )}
      </div>
    </section>
  `;
};
