import { html, render } from 'lit-html';
import Navigo from 'navigo';
import { Home } from './components/Home.js';
import { Menu } from './components/Menu.js';
import { Cart } from './components/Cart.js';
import { state } from './state/state.js';
import axios from 'axios';

// Initialize the router
const router = new Navigo('/', { hash: true });

const store = {
  home: Home,
  menu: Menu,
  cart: Cart,
};

// Define a function to render the views
const renderView = (view) => {
  render(store[view](), document.querySelector('#root'));
};

// Configure the router hooks
router.hooks({
  before: (done, match) => {
    const view = match?.data?.view ? camelCase(match.data.view) : 'home';

    switch (view) {
      case 'menu':
        axios.get('http://localhost:5000/api/pizzas')
          .then(response => {
            state.pizzas = response.data.map(pizza => ({
              ...pizza,
              selectedToppings: [...pizza.toppings],
            }));
            done();
          })
          .catch(error => {
            console.error('Error fetching pizza data:', error);
            done();
          });
        break;
      default:
        done();
    }
  },
  already: (match) => {
    const view = match?.data?.view ? camelCase(match.data.view) : 'home';
    renderView(view);
  },
  after: (match) => {
    router.updatePageLinks();
    const bars = document.querySelector('.fa-bars');
    if (bars) {
      bars.addEventListener('click', () => {
        document.querySelector('nav > ul').classList.toggle('hidden--mobile');
      });
    }
  }
});

// Define your routes
router.on({
  '/': () => renderView('home'),
  '/menu': () => renderView('menu'),
  '/cart': () => renderView('cart'),
}).resolve();

// Function to convert route names to camel case
function camelCase(str) {
  return str.replace(/-([a-z])/g, (g) => g[1].toUpperCase());
}

// Export renderApp for state.js
export const renderApp = () => {
  renderView(state.currentPage);
};

renderView('home');
